@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search{
    position: absolute;
    background-color: white;
    /* border: 2px solid black; */

    top:8px;
    left:400px;
    z-index: 1;
}

#search-results > *{
    margin: 5px auto;
    border-bottom: 1px solid grey;
    border-top: 1px solid grey;
    border-right: 1px solid grey;
    border-left: 1px solid grey;
}

#search-results a{
    text-decoration: none;
}
.border{
    border: 1px solid black;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

/* -----------------------------------Header---------------------------------------------------- */
.header {
  width: 100%;
  height: 60px;
  display: flex;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  align-items: center;
  justify-content: space-between;
}
.header .title {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .title h2 {
  text-decoration: none;
}
.header .title img {
  width: 60px;
  height: 60px;
  margin-left: 20px;
}

.header h2 {
  padding-left: 20px;
  font-size: 1.8rem;
  cursor: pointer;
  padding-top: 5px;
}

.header input {
  width: 400px;
  font-size: 1rem;
  padding: 8px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border: 0;
}

.header input:focus {
  outline-color: dodgerblue;
}

.header ul {
  list-style-type: none;
  margin-right: 30px;
  padding: 0;
  font-size: 1.3rem;
  display: inline-block;
  margin-top: 10px;
}
.header svg {
  width: 25px;
  height: 25px;
  margin: 5px 15px 5px 10px;
  cursor: pointer;
}
li {
  float: left;
}

a {
  display: block;
  padding: 8px;
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.right-nav {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ----------------------------------------------------------------------------------------------------------------- */
/* ----------------------------------------------------------Front Page---------------------------------------------- */

.front {
  width: 100%;
  height: 700px;
  display: flex;
}

.left {
  width: 50%;
  height: 90%;
  margin-right: 10%;
}
.left .content {
  width: 450px;
  height: 200px;
  margin: 20% auto;
}
.left .content h1 {
  font-size: 3rem;
}
.left .content p {
  margin-top: 10px;
  font-size: 2rem;
}

.right {
  width: 45%;
  height: 90%;
  padding-right: 100px;
}

.right .card {
  width: 600px;
  height: 500px;
  margin: 10% auto;
  background-image: url("https://images.pexels.com/photos/889839/pexels-photo-889839.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500");
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 10px;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
}
.right .card:hover {
  cursor: pointer;
  opacity: 0.8;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

#explore {
  background-color: dodgerblue;
  color: #fff;
  padding: 5px 10px 5px 10px;
  font-size: 1.1rem;
  border-radius: 5px;
  margin-top: 25px;
  width: 150px;
  height: 40px;
  font-weight: 100;
  letter-spacing: 2px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  border-color: dodgerblue;
  border-width: 0px;
}
#explore:hover {
  cursor: pointer;
  background-color: rgb(10, 110, 209);
}

/* ---------------------------------------------------------------------------------------------------------- */
.div {
  text-align: center;
}
.card-component {
  width: 24%;
  height: 500px;
  display: inline-block;
  margin-right: 2%;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
}
.card-component:hover {
  cursor: pointer;
  opacity: 0.9;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}

.cards {
  width: 78%;
  height: 550px;
  white-space: nowrap;
  margin-left: 11%;
  margin-right: 11%;
  margin-top: 5%;
}
.card-component .art-image {
  width: 100%;
  height: 350px;
}
.card-component img {
  width: 100%;
  height: 100%;
}
.card-component .card-content {
  height: 150px;
  padding-top: 30px;
  text-align: center;
  font-size: 1.5rem;
  background-color: rgba(60, 96, 194, 0.966);
  color: #fff;
  overflow: hidden;
}
.card-component .card-content h5 {
  margin-top: 10px;
}

/* --------------------------------------------------------------------------------------------------------------- */

.div2 {
  text-align: center;
  margin-top: 70px;
  margin-bottom: 60px;
}
.category {
  margin-top: 30px;
  height: 700px;
  width: 66%;
  margin-right: 20%;
  margin-left: 18%;
  display: flex;
  flex-flow: column wrap;
}
.category .category-card {
  margin: 20px;
  margin-top: 50px;
  transition: -webkit-transform 250ms linear;
  transition: transform 250ms linear;
  transition: transform 250ms linear, -webkit-transform 250ms linear;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;

  flex: 200px 1;
}

.category .category-card .catCard {
  width: 100%;
  height: 80%;
  border-radius: 10%;
}
.category .category-card .catCard img {
  width: 100%;
  height: 100%;
}
.category .category-card h4 {
  text-align: center;
  font-size: 1.5rem;
  margin-top: 15px;
  padding-bottom: 10px;
}
.category-card:hover {
  cursor: pointer;
  opacity: 0.9;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

/* ---------------------------------------------------------------------------------------------------------------- */

.footer-div .first-div {
  background-color: rgb(6, 67, 128);
  width: 100%;
  height: 30px;
}
.footer-div {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin-top: 100px;
  width: 100%;
  height: 410px;
  background-color: rgb(13, 117, 221);
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px,
    rgba(0, 0, 0, 0.05) 0px 5px 10px;
}
.team-container {
  float: left;
  width: 50%;
  height: 250px;
}
.team-container .dheet-div {
  margin-left: 110px;
  margin-top: 30px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}
.team-container .dheet-div svg {
  margin-right: 15px;
  width: 30px;
  height: 30px;
}
.footer-div ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.footer-div li {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
}
.footer-div a {
  color: rgba(255, 255, 255, 0.904);
  display: inline-block;
}
.footer-div svg {
  margin-right: 3px;
}
.footer-div .members {
  display: flex;
  justify-content: space-evenly;
}

.contact-div {
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-div input,
textarea {
  width: 60%;
  padding: 10px;
  font-size: 1rem;
  margin-top: 10px;
  border: 0;
}
.contact-div input:focus,
textarea:focus {
  outline-color: dodgerblue;
  outline: none;
}
.contact-div textarea {
  height: 100px;
}
.contact-div button {
  padding: 5px 10px 5px 10px;
  align-self: flex-start;
  margin-left: 151.5px;
  margin-top: 10px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  background-color: dodgerblue;
  color: #fff;
  border: 0;
  font-size: 1.2rem;
  width: 80px;
}
.contact-div button:hover {
  cursor: pointer;
  opacity: 0.8;
}
.contact-div .contact-head {
  display: flex;
  align-items: center;
  margin-left: 150px;
  margin-top: 30px;
  margin-bottom: 10px;
  align-self: flex-start;
}
.contact-div .contact-head h2 {
  margin-left: 10px;
}
.contact-div .contact-head svg {
  color: white;
  width: 30px;
  height: 30px;
}
.footer-div .last {
  width: 90%;
  height: 20%;
  border: 2px solid rgba(255, 255, 255, 0.671);
  border-bottom: 0;
  border-left: 0;
  border-right: 0;
  margin-left: 5%;
  margin-top: 80px;
  display: flex;
  justify-content: space-between;
  padding: 5px;
}
#one-last {
  margin-top: 7px;
}
#another-last {
  display: flex;
  align-items: center;
}
#another-last svg {
  margin-left: 5px;
}

