#modal {
  position: absolute;
  top: 30%;
  left: 50%;
  width: 50%;
  height: 50%;
  padding: 30px;
  overflow: auto;
  background-color: white;
  z-index: 2;
  /* box-shadow: 0 0 20px 1px black; */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 10px;
}
.info div {
  width: 100%;
  margin: 2.5px 0;
  padding: 4px;
}
#modal_bg {
  position: fixed;
  min-height: 100%;
  width: 100vw;
  background-color: black;
  top: 0;
  left: 0;
  opacity: 0.5;
  z-index: 1;
}
#close_button {
  position: absolute;
  right: 10px;
  top: 5px;
  -webkit-transform: rotateZ(45deg);
          transform: rotateZ(45deg);
  font-size: 20px;
}
#close_button:hover {
  cursor: pointer;
}
.hide {
  visibility: hidden;
}

